/**
 * Estilização do app
 */
.reset-button,
button,
.navbar-toggle {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.clearfix:after,
.navbar-control:after {
  content: "";
  display: table;
  clear: both;
}
@font-face {
  font-family: 'ebenezer';
  src: url("../fonts/ebenezer.eot");
  src: url("../fonts/ebenezer.eot#iefix") format('embedded-opentype'), url("../fonts/ebenezer.woff") format('woff'), url("../fonts/ebenezer.ttf") format('truetype'), url("../fonts/ebenezer.svg#ebenezer") format('svg');
  font-weight: normal;
  font-style: normal;
}
@import url("https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans:400,700");
[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "ebenezer";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  font-size: 120%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(127,127,127,0.3);
}
* {
  box-sizing: border-box;
}
html {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}
::selection {
  background: #7d965d;
  color: #fff;
}
:focus {
  border: 0;
  outline: 2px auto #333;
}
h1,
h2 {
  margin-top: 0;
  font-family: 'Montserrat';
  font-weight: 700;
  text-transform: uppercase;
}
h1 {
  margin-bottom: 30px;
  font-size: 24px;
  text-align: center;
}
h2 {
  margin-bottom: 30px;
  font-size: 20px;
}
p {
  line-height: 160%;
  margin-bottom: 30px;
}
ul {
  padding: 0;
  margin: 0;
}
a {
  color: #7e9659;
  text-decoration: none;
}
a:hover,
a:focus,
a:active {
  text-decoration: underline;
}
li {
  list-style: none;
  margin-bottom: 0;
}
object {
  position: absolute;
  left: -1000px;
}
._img-responsive {
  max-width: 100%;
  display: block;
  height: auto;
}
._text-right {
  text-align: right;
}
._overflow-hidden {
  overflow-x: hidden;
}
._section-site {
  margin-top: 25px;
}
._center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
._btn-margin {
  margin-top: 40px;
}
@media (min-width: 1200px) {
  .__invisible-lg {
    display: none !important;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .__invisible-md {
    display: none !important;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .__invisible-sm {
    display: none !important;
  }
}
@media (min-width: $screen-xs-min) and (max-width: 767px) {
  .__invisible-vs {
    display: none !important;
  }
}
@media (max-width: $screen-xs) {
  .__invisible-xs {
    display: none !important;
  }
}
.btn-default {
  padding: 8px 35px;
  font-weight: bold;
  color: #7e9659;
  border-radius: 20px;
  border: 2px solid;
  border-color: #7e9659;
  transition: 0.2s all linear;
  display: inline-block;
}
.btn-default:hover,
.btn-default:focus,
.btn-default:active {
  background: #7e9659;
  color: #fff;
  outline: 0;
}
a.btn-default {
  text-decoration: none;
}
a.btn-default:hover,
a.btn-default:focus,
a.btn-default:active {
  text-decoration: none;
}
@media (min-width: 1200px) {
  .container-nav {
    margin-top: 90px;
    overflow: hidden;
  }
}
.embed-responsive {
  height: 0;
  padding-bottom: 56.25%;
  display: block;
  position: relative;
  overflow: hidden;
}
.embed-responsive > .media {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
.footer-site {
  padding: 15px 0;
  margin-top: 90px;
  background-color: #e1e3da;
}
.footer-site .phrase {
  position: relative;
  margin-left: 15px;
}
.footer-site .phrase:before,
.footer-site .phrase:after {
  font-family: 'fontAwesome';
  position: absolute;
}
.footer-site .phrase:before {
  content: '\f10d';
  top: -15px;
  left: -15px;
}
.footer-site .phrase:after {
  content: '\f10e';
  bottom: -15px;
}
.footer-site .facebook {
  margin-top: 0;
}
.footer-site .facebook a {
  color: inherit;
}
.footer-site .location {
  margin-top: 50px;
  margin-bottom: 0;
}
@media (min-width: 992px) {
  .footer-site .facebook,
  .footer-site .location {
    text-align: right;
  }
}
.form-control {
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #ababab;
  background-color: #fff;
  background-image: none;
  border: 1px solid 1px #ababab solid;
  border-radius: 4px;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}
.form-control:focus {
  border-color: #66afe9;
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.075), 0 0 8px rgba(102,175,233,0.6);
}
::-webkit-input-placeholder {
  color: #ababab;
  opacity: 1;
}
::-moz-placeholder {
  color: #ababab;
  opacity: 1;
}
:-ms-input-placeholder {
  color: #ababab;
  opacity: 1;
}
.form-search {
  margin-bottom: 20px;
}
.input-group {
  display: table;
  position: relative;
}
.input-group.-full {
  width: 100%;
}
.input-group > .addon {
  width: 1%;
  display: table-cell;
}
.input-group > .addon > .button {
  padding: 6px 12px;
  font-size: 14px;
  color: #ababab;
  line-height: 1.42857143;
  border-radius: 4px;
  border: 1px #ababab solid;
  border-left: 0;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  transition: 0.2s all linear;
  display: inline-block;
}
.input-group > .addon > .button:before {
  transition: 0.2s transform linear;
}
.input-group > .addon > .button:hover,
.input-group > .addon > .button:focus,
.input-group > .addon > .button:active {
  outline: 0;
  color: #555;
}
.input-group > .addon > .button:hover:before,
.input-group > .addon > .button:focus:before,
.input-group > .addon > .button:active:before {
  transform: scale(1.2);
  transform-origin: center;
}
.header-site {
  background-color: #e1e3da;
  padding-top: 15px;
}
.info-icon > .icon,
.info-icon > .title {
  text-align: center;
  margin-bottom: 20px;
}
.info-icon > .icon {
  font-size: 140px;
  display: block;
}
.info-icon > .title {
  font-size: 16px;
  font-family: 'Montserrat';
}
.internal-banner {
  margin-top: 20px;
  background-color: #848973;
  min-height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.internal-banner .title {
  color: #fff;
  margin: 0 0 40px 0;
  font-size: 3.75rem;
}
.internal-banner .description {
  color: #fff;
  font-size: 1.25rem;
}
.internal-content {
  margin-top: 20px;
}
.internal-content h1 {
  font-size: 1.75rem;
}
.jump-to {
  position: absolute;
  z-index: 999999;
}
.jump-to > .link {
  width: 1px;
  height: 1px;
  padding: 5px;
  left: -10000px;
  background-color: #7d965d;
  position: absolute;
  overflow: hidden;
}
.jump-to > .link:focus,
.jump-to > .link:active,
.jump-to > .link:hover {
  width: auto;
  height: auto;
  left: 0;
  color: #fff;
  text-decoration: none;
  position: static;
  overflow: visible;
}
.list-categories > .item {
  margin-right: 10px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  display: inline-block;
}
.list-categories > .item:before {
  content: '•';
  margin-right: 5px;
}
.list-events > .item {
  margin-bottom: 20px;
  border-bottom: 1px solid #7d965d;
}
.list-events > .item > .date {
  font-size: 12px;
  display: block;
}
.list-events > .item > .link {
  width: 100%;
  padding-bottom: 15px;
  font-weight: bold;
  color: #7d965d;
  text-decoration: none;
  display: inline-block;
}
.list-events > .item > .link:hover,
.list-events > .item > .link:focus,
.list-events > .item > .link:active {
  color: #586941;
  text-decoration: none;
  outline: 0;
}
.list-events > .item > .link > .icon {
  margin-left: -7px;
}
.list-videos > .more {
  padding: 10px;
  text-align: center;
  font-weight: bold;
  border-top: 1px solid #7e9659;
  display: block;
}
.list-videos > .more:hover,
.list-videos > .more:focus,
.list-videos > .more:active {
  outline: 0;
}
.logo-site {
  display: inline-block;
  padding-bottom: 15px;
}
.owl-buttons {
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  margin: auto;
}
.owl-buttons .icon {
  font-size: 70px;
  color: #fff;
}
.owl-buttons .icon:hover,
.owl-buttons .icon:focus,
.owl-buttons .icon:active,
.owl-buttons .icon:active:focus {
  color: #7d965d;
  transition: all 0.1s ease-in-out;
  opacity: 0.8;
}
.owl-buttons .owl-prev,
.owl-buttons .owl-next {
  top: -35px;
  position: absolute;
}
.owl-buttons .owl-prev {
  left: 0;
}
.owl-buttons .owl-next {
  right: 0;
}
.owl-pagination {
  width: 100%;
  position: absolute;
  bottom: 10px;
  text-align: center;
}
.owl-pagination .owl-page {
  display: inline-block;
  margin-right: 10px;
}
.owl-pagination .owl-page span {
  background-color: #fff;
  width: 15px;
  height: 15px;
  display: inline-block;
  border-radius: 50%;
}
.owl-pagination .owl-page:hover span,
.owl-pagination .owl-page.active span {
  background-color: #7d965d;
}
.nav-site > .item > .link {
  font-size: 14px;
  font-weight: bold;
  color: #333;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  display: block;
}
@media (min-width: 1200px) {
  .nav-site {
    text-align: right;
  }
  .nav-site > .item {
    display: inline-block;
    margin-right: 15px;
  }
  .nav-site > .item:last-child {
    margin-right: 0;
  }
  .nav-site > .item > .link {
    padding-bottom: 15px;
  }
  .nav-site > .item > .link:after {
    content: '';
    width: 100%;
    height: 6px;
    bottom: 0;
    left: 0;
    background-color: #848973;
    transform: translateY(7px);
    transform-origin: bottom;
    transition: 0.2s all linear;
    position: absolute;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
    color: #848973;
    outline: 0;
  }
  .nav-site > .item > .link:hover:after,
  .nav-site > .item > .link:focus:after,
  .nav-site > .item > .link:active:after {
    transform: translateY(0);
  }
}
.navbar-control {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.navbar-control:not(.-close) {
  margin-top: 75px;
  margin-left: 20px;
}
.navbar-control > .text {
  margin: 0 5px 0 0;
  font-size: 14px;
  color: #333;
}
.navbar-toggle {
  width: 52px;
  height: 40px;
  padding: 9px 10px;
  position: relative;
}
.navbar-toggle > .icon-bar {
  display: block;
  width: 30px;
  height: 3px;
  border-radius: 4px;
  background-color: #333;
  transition: all 0.2s linear;
}
.navbar-toggle > .icon-bar:nth-child(2) {
  opacity: 1;
}
.navbar-toggle .icon-bar + .icon-bar {
  margin-top: 4px;
}
.post-summary {
  margin-bottom: 20px;
}
.post-summary > .date {
  font-size: 12px;
  font-weight: bold;
}
.post-summary > .title {
  margin: 0;
  font-family: 'Montserrat';
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  color: #7e9659;
}
.post-summary > .link {
  text-decoration: underline;
}
.sr-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.video-item {
  margin-bottom: 15px;
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-start;
}
.video-item > .link {
  margin-right: 5px;
}
.video-item > .content > .title {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: 400;
  font-family: 'Open Sans';
  text-transform: none;
}
.video-item > .content > .title > .link {
  text-decoration: none;
  color: inherit;
}
.video-item > .content > .date {
  font-size: 12px;
}
.video-site {
  margin-bottom: 20px;
  display: block;
  position: relative;
}
.video-site .embed {
  display: none;
}
.video-site .embed.js-visible {
  display: block;
}
.video-site > .link > .image,
.video-site > .image {
  width: 100%;
}
.video-site > .link {
  display: block;
  position: relative;
}
.video-site > .link.js-hidden {
  display: none;
}
.video-site > .link .icon {
  content: '\e80b';
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0.2);
  color: #fff;
  font-size: 110px;
  display: flex;
  justify-content: center;
  position: absolute;
  z-index: 2;
}
.video-site > .link .icon:before {
  line-height: 2.7;
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  transition: all linear 0.4s;
}
.video-site > .link .icon.js-active:before {
  transform: scale(1.5);
  opacity: 0;
  visibility: hidden;
  transition: all linear 0.4s;
}
.video-site > .label {
  width: 100%;
  padding: 10px 15px;
  bottom: 0;
  left: 0;
  color: #fff;
  text-align: center;
  background-color: rgba(0,0,0,0.7);
}
.video-site > .label > .description {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}
.video-site > .label > .description > .title {
  margin: 0;
  font-size: 20px;
  font-weight: 700;
  font-family: 'Open Sans';
  text-transform: none;
  text-align: left;
  line-height: normal;
}
.video-site > .label .link {
  color: #fff;
}
.video-site > .label .link:hover,
.video-site > .label .link:focus,
.video-site > .label .link:active {
  color: #fff;
  text-decoration: none;
}
@media (min-width: 768px) {
  .video-site > .label {
    position: absolute;
    z-index: 3;
  }
}
@media (max-width: 1199px) {
  .video-site > .link .icon:before {
    line-height: 2;
  }
}
@media (max-width: 519px) {
  .video-site > .link .icon {
    font-size: 80px;
  }
}
.input-group .form-control {
  display: table-cell;
  border-right: 0;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.wrapper-site {
  transition: 0.4s transform ease-in-out;
}
.wrapper-site.js-active-menu {
  height: 100%;
  transition: 0.4s transform ease-in-out;
  transform: translateX(-200px);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(1) {
  transform: rotate(45deg);
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(2) {
  width: 0;
  height: 0;
  margin: 0;
  opacity: 0;
}
.wrapper-site.js-active-menu .navbar-toggle > .icon-bar:nth-child(3) {
  margin-top: -3px;
  transform: rotate(-45deg);
}
@media (max-width: 1199px) {
  .js-ie .wrapper-site.js-active-menu .nav-site {
    transition: 0.4s transform ease-in-out;
    transform: translateX(0px);
    opacity: 1;
  }
  .js-ie .nav-site {
    overflow-x: hidden;
    overflow-y: auto;
    transition: 0.4s transform ease-in-out;
  }
  .js-ie .nav-site._no-opacity {
    opacity: 0;
  }
  .nav-site {
    width: 200px;
    height: 100%;
    min-height: 100vh;
    min-height: 100vmax;
    top: 0;
    right: 0;
    background-color: #e1e3da;
    transform: translateX(200px);
    visibility: hidden;
    position: fixed;
  }
  .nav-site.js-active {
    visibility: visible;
  }
  .nav-site > .item > .link {
    padding: 15px;
  }
  .nav-site > .item > .link:hover,
  .nav-site > .item > .link:focus,
  .nav-site > .item > .link:active {
    color: #fff;
    background-color: #848973;
    outline: 0;
  }
}
